.header {
  background-color: var(--color-black);
  padding: 37px 0;

  &__logo {
    margin-right: 60px;
  }

  &__container {
    @include flex-v-center;
  }

  &__nav {
    margin-right: auto;
  }
}

@media (max-width: 768px) {
  .header {
    &__logo {
      margin-right: 20px;
    }
  }
  
}